import React from 'react'
import Input from './Input'

const LoginForm = (props) => {
    return (
        <form className="form">
            <Input type="text" placeholder="Username" autoComplete={props.autoComplete} />
            <Input type="password" placeholder="Password" autoComplete={props.autoComplete} />
            {props.isRegistered === false && <Input type="password" placeholder="re-password" autoComplete={props.autoComplete} />}
            <button type="submit">{props.isRegistered ? "Login" : "Register"}</button>
        </form>
    )
}

export default LoginForm
