import React from 'react'
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

const Note = ({id, title, content, onDel}) => {
    return (
        <div className="note">
            <h1 value={id}>{title}</h1>
            <p>{content}</p>
            <button onClick={()=>{
                onDel(id)
            }}>
                <span><DeleteForeverIcon /></span>
            </button>
        </div>
    )
}
export default Note;