import React, {useState} from 'react'

const InputForm = (props) => {
    const [inputText, setInputText] = useState('')
    const handleInput = (e) => {
        const {value} = e.target;

        setInputText(value)
    }
    return (
        <div className="form" >
            <input onChange={handleInput} 
            type="text" 
            name="txtInput" 
            value={inputText} />
            <button onClick={()=>{
                props.onInput(inputText)
                setInputText("")
            }}>
                <span>Add</span>
            </button>
        </div>
    )
}

export default InputForm
