import React, {useState} from 'react'
import Note from '../components/Note'
import AddNote from '../components/AddNote'
//import notes from '../notes'

// Challenge:
// 1. Implement the add note functionality
// Create a constant that keeps track of the title and content
// Pass the new note to the parent component 
// Add the new note to an array
// Take the array and render separate Note components for each item

// 2. Implement the delete note functionality
// Callback from the note component to trigger a delete function on the parent component
// Use the filter function to filter out the item that needs the deletion
// Pass a id over to the Note component, pass it back to the parent component 



const Home = () => {
    const [noteData, setData] = useState([])
    const handleAddNote = (newNote) =>{
        setData((prevValue)=>{
            return [...prevValue, newNote]
        })
        
    }
    const handleDeleteNote = (id) =>{
        setData((prevValue)=>{
            return prevValue.filter((item, index)=>{
                return index !== id
            }) 
        })
    }
    return (
        <div>
            <AddNote onAdd={handleAddNote}/>
            {noteData.map(
                (note, index) => <Note 
                key={index}
                id={index}
                title={note.title}
                content={note.content}
                onDel={handleDeleteNote}
                />
            )}
        </div>
    )
}

export default Home
