import React, {useState} from 'react'
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';

const AddNote = (props) => {
    const [isFocused, setIsFocused] = useState(false)

    const [newNote, setInputText] = useState({
        title: "",
        content: ""
    })

    const handleChange = (e) => {
        const {name, value} = e.target
        setInputText(
            (prevValue) => {
                return {
                    ...prevValue,
                    [name]: value
                }
            }
        )
    }

    const readyToTakeNote = () => {
        setIsFocused(true)
        
    }
    return (
        <div className="addnote-container">
            {isFocused && <input 
            onChange={handleChange} 
            type="text" 
            name="title" 
            placeholder="Title" 
            value={newNote.title} 
            autoFocus="true" />}
            <textarea 
            onClick={readyToTakeNote}
            onChange={handleChange} 
            name="content" 
            placeholder="Take a note" 
            rows={isFocused? 3:1} 
            value={newNote.content} 
            /> 
            <Zoom in={isFocused}>
            <Fab onClick={()=>{
                props.onAdd(newNote)
                setInputText({
                    title: "",
                    content: ""
                })
                setIsFocused(false)
            }}><AddIcon />
            </Fab>
            </Zoom>
        </div>
    )
}

export default AddNote
